// Styles
import './styles/main.css'
import logoURL from './logo/hercules.png';

// Setup semantic UI
import 'semantic-ui/dist/semantic.min.js';
import 'semantic-ui/dist/semantic.min.css';

import { Elm } from './src/Main.elm'

var app = Elm.Main.init({
  node: document.getElementById('main'),
  flags: {
    apiURL: window.location.origin + "/api/v1",
    logoURL: logoURL
  }
});

// TODO: unique identifier for each dropdown
// once DOM for dropdown is rendered, we have to trigger JS init
app.ports.initDropdown.subscribe(function(data) {
  setTimeout(function() {
    $('.ui.dropdown').dropdown().on('click', '.item', function(e) {
      var value = $(this).attr('data-value');
      app.ports.onChangeDropdown.send(value);
    });
  }, 800);
});
